*,
*:before,
*:after {
    box-sizing: border-box;
}

html,
body,
div,
span,
object,
iframe,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
code,
em,
img,
small,
strike,
strong,
sub,
sup,
tt,
b,
u,
i,
ol,
ul,
li,
fieldset,
form,
label,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
main,
canvas,
embed,
footer,
header,
nav,
section,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    text-size-adjust: none;
}

footer,
header,
nav,
section,
main {
    display: block;
}

body {
    line-height: 1;
    font-family: 'Montserrat', sans-serif;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

input,
button {
    -webkit-appearance: none;
    border-radius: 0;
    border: 0;
}

button,
[type='submit'] {
    cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-bottom: 0.5rem;
}

#pampa {
    border-right: 2px solid;
    padding-right: 0.5rem;
    margin-right: 0.5rem;
    font-weight: 700;
}
header {
    position: fixed;
    z-index: 100;
    width: 100%;
    & p {
        font-size: 0.875rem;
        font-style: italic;
    }
}
#black-prev {
    padding-right: 0;
}

#black-next {
    padding-left: 0;
}

#white-prev img {
    transform: rotate(90deg);
}
#white-next img {
    transform: rotate(-90deg);
}
